import { Component, OnInit } from '@angular/core';
import { UserService } from '@services/modules/user/services/user.service';
import { CustomerInfo } from '@shared/schemas/tz-customer-info';

@Component({
    selector: 'tz-delete-account',
    templateUrl: './delete-account.component.html',
    styleUrls: ['./delete-account.component.scss'],
})
export class DeleteAccountComponent implements OnInit {
    public customerInformation!: CustomerInfo;

    constructor(private userService: UserService) {}

    ngOnInit() {
        this.customerInformation = this.userService.getCustomerInformation();
    }
}
